body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  background-color: white;
  color: #e7e7e7;
  margin: 0 auto;
  color: white;
  font-family: 'FiraCode-retina';
}


.flip-container {
  -webkit-perspective: 1000px;
          perspective: 1000px;
  border: 1px solid darkgray;
  background: white;
  border-radius: 10px;
  padding: 10px;
  margin: 5px auto;
}

.flip-container.flipped .flipper {
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}

.flipper {
  transition: 0.4s;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  position: relative;
}

.front,
.back {
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
}

.back {
  z-index: 2;
  -webkit-transform: rotateY(0deg);
          transform: rotateY(0deg);
  cursor: pointer;
}

.front {
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}
body {
  width: 100%;
  text-align: center;
}

.board {
  width: 730px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-align-items: center;
          align-items: center;
  margin-left: auto;
  margin-right: auto;
}

h1{
  text-align: center;
  color: aquamarine;
  font-size: 48px;
  margin-bottom: 0;
}
p, label{
  color: crimson;
  font-size: 20px;
  display: inline-block;
}
.score{
    font-size: 24px;
}
input{
  border: 1px solid aquamarine;
  margin-bottom: 20px;
  outline: none;
  font-size: 18px;
  padding: 5px 1px;
  margin-left: 20px;
  text-align: center;
  color: crimson;
}
input:focus {
  border: 2px solid aquamarine;
  padding: 4px 0;
}
.hidden{
  display: none;
}
button{
  text-decoration: none;
  outline: none;
  font-family: Onyx;
  border: 2px solid aquamarine;
  color: gray;
  background-color: white;
  border-radius: 5px;
  font-size: 20px;
  padding: 5px 7px;
  margin-bottom: 20px;
  cursor: pointer;
}
button:hover,
button:focus {
  border: 3px solid aquamarine;
  padding: 4px 7px;
}
button:active {
  -webkit-transform: scale(0.99);
          transform: scale(0.99);
}


